.StartPage {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.pointsContainer {
  margin-top: 28px;
  padding: 20px 8px;
  border-radius: 11px;
  transition: var(--anim);
  @media (max-width: 600px) {
    margin-top: 12px;
  }
}
